.banner h2 {
  padding: 0 1rem 0 1rem;
  margin-top: 5rem;
  white-space: nowrap;
  display: inline-block;
  font-size: 15vw;
  background-color: var(--warna-biru);
  color: var(--warna-cream);
  transform: rotate(3deg) skew(-0.312rad);
}
@media screen and (min-width: 64rem) {
  .banner h2 {
    font-size: 8vw;
  }
}

.banner2 h2 {
  padding: 0 1rem 0 1rem;
  margin-top: 5rem;
  white-space: nowrap;
  display: inline-block;
  font-size: 15vw;
  background-color: #1c6dd0;
  color: var(--warna-cream);
  transform: rotate(-3deg) skew(0.312rad);
}
@media screen and (min-width: 64rem) {
  .banner2 h2 {
    font-size: 8vw;
  }
}